.wrapper {
  margin: 2rem;
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: 1fr;
  justify-content: center;
}

.card {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 0.2rem;
  border-radius: var(--radius);
  min-width: 28rem;
  display: flex;
  background-color: #afa281;
  font-weight: 200;
}
.card__img {
  width: 14rem;
  height: 11rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  font-weight: bolder;
  color: white;
  
}

.card__body {
  color: #152236;
  margin: 1rem;
  flex-grow: 1;
  text-align: left;
}
.card__title {
  line-height: 1.4rem;
  margin-bottom: 0.5rem;
}
.card__description {
  line-height: 1.2rem;
}
.card__content {
  font-size: 1.4rem;
  font-weight: bold;
  margin-top: 0.6rem;
}
.card .car__price::before {
  content: "$";
  font-size: 1rem;
  position: relative;
  top: -0.3rem;
  padding-right: 0.1rem;
}

.card__btn {
  border: none;
  border-top: 1px solid #e1e5ee;
  border-top: 1px solid var(--clr-gray300);
  background-color: transparent;
  font-family: inherit;
  font-size: 1rem;
  font-weight: bold;
  color: inherit;
  width: 100%;
  padding-top: 1rem;
  margin-top: 1rem;
  cursor: pointer;
}
.card__btn:hover{
   position: relative;
   -webkit-transform: translateX(8px);
           transform: translateX(8px);
   transition: all 0.7s ease-in-out;
}
/* .card:hover {
  color: var(--clr-primary);
} */

@media screen and (min-width: 600px) {
  .wrapper {
    grid-template-columns: repeat(auto-fit, minmax(14rem, 16rem));
  }

  .card {
    flex-direction: column;
    text-align: center;
    min-width: 14rem;
  }
  .card__img {
    width: 100%;
    height: 12rem;
  }
}

